.gpt3_possibility {
    display: flex;
}
.gpt3__possibility-image{
    flex: 1;
    display: flex;
    justify-self: flex-start;
    align-items: center;

    margin-right: 2rem;
}
.gpt3__possibility-image img{
    width: 100%;
    height: 100%;
}
.gpt3__possibility-content{
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;

}
.gpt3__possibility-content h4{
    font-family: 'Manrope';
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 30px;
/* identical to box height, or 188% */


color: #71E5FF;
}
.gpt3__possibility-content h4:last-child{
    color: #FF8A71
}
.gpt3__possibility-content h1{
    font-family: 'Manrope';
font-style: normal;
font-weight: 800;
font-size: 34px;
line-height: 45px;
margin: 1rem 0;
}
.gpt3__possibility-content p{
    font-family: 'Manrope';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 30px;
/* or 188% */
margin-bottom: 2rem;


color: #81AFDD;
}
@media screen and (max-width:950px) {
    .gpt3_possibility{
        flex-direction: column;
    }
    .gpt3__possibility-image{
        margin: 1rem 0;
    }

    .gpt3__possibility-content{
        margin-top: 2rem;
    }
}
